/*** Menu **/

.menu {
  position: relative;
  background-color: #004b8c;

  width: 100%;

  /*
	width:85%;
	max-width:1024px;
  */

  display: inline-block;
  width: 200%;
  position: relative;
  left: -50%;
}

.menu:after {
  content: "";
  display: block;
  position: absolute;
  width: 3px;
  background-color: #ffc107;
  height: 100%;

  top: 0;
  right: 3%;
}

.menu:before {
  content: "";
  display: block;
  position: absolute;
  width: 3px;
  background-color: RED;
  height: 100%;

  top: 0;
  right: 2%;
}

.menu .title {
  color: #fff;
  font-size: 2rem;
  font-family: "Open Sans", sans-serif;
  margin: 0.8em 0 0 0;
}

.menu ul {
  list-style: none;
  margin: 20px 0 0 0;
  padding: 0;
  text-align: center;
}

.menu li {
  display: inline-block;
  text-align: center;
  width: 150px;
}

.menu li:hover {
  background-color: #0f74cc;
}

.menu a,
.menu label {
  text-decoration: none;
  color: WHITE;
  display: inline-block;
  cursor: pointer;
  padding-top: 2px;
  padding-bottom: 2px;

  position: relative;
  min-width: 100px;
  width: auto;

  height: 30px;
  line-height: 30px;
  cursor: pointer;
  padding: 2px 10px;
}

.menu a:hover {
}

#chkMenu {
  display: none;
}

.btnMenu {
  display: none;
  position: fixed;
  top: 15px;
  left: 15px;
  z-index: 1;
}
.btnMenu i {
  cursor: pointer;
}

@media only screen and (max-width: 520px) {
  p {
    text-align: justify;
    text-indent: 1.5rem;
    font-size: 0.8rem;
  }

  .menu:after {
    width: 100%;
    height: 2px;

    top: 45px;
    left: 0;
  }

  .menu:before {
    width: 100%;
    height: 2px;

    top: 40px;
    left: 0;
  }

  .menu .title {
    position: absolute;
    top: 0;
    left: 0;
  }

  .btnMenu {
    display: inline-block;
  }

  .mainContainer {
    margin-top: 50px;
  }

  .menu {
    position: fixed;
    top: 0;

    z-index: 10;

    width: 50%;
    min-width: 250px;

    height: 100%;

    transition: left 500ms linear;
    -webkit-transition: left 500ms linear;
    box-shadow: 2px 0 10px rgba(0, 0, 0, 1);
  }

  #chkMenu + .menu {
    display: none;

    left: -90%;
  }

  #chkMenu:checked + .menu {
    display: block;
    left: 0;
  }

  .menu ul {
    margin-top: 50px;
  }

  .menu li {
    display: block;
  }

  .menu a {
    width: 80%;
    text-align: left;
  }

  p {
    text-align: left;
    text-indent: inherit;
    font-size: 1em;
  }
}

