* {
  font-family: "Lato", sans-serif;
}

body,
html {
  padding: 0;
  margin: 0;

  background: #fbfbfb;
  background: -moz-linear-gradient(top, #fbfbfb 0%, #ededed 46%, #ededed 100%);
  background: -webkit-linear-gradient(
    top,
    #fbfbfb 0%,
    #ededed 46%,
    #ededed 100%
  );
  background: linear-gradient(to bottom, #fbfbfb 0%, #ededed 46%, #ededed 100%);

  text-align: center;
}

main {
  width: 970px;
  max-width: 100%;

  position: relative;

  text-align: left;
  display: inline-block;

  background-color: WHITE;

  min-height: 700px;

  margin-bottom: 30px;
}

.divMain {
  width: inherit;
  max-width: inherit;
  position: relative;
}

header {
  height: 122px;
  width: 100%;

  background-color: #223d84;
}

h2 {
  margin: 20px;
  border-bottom: 1px solid #abc1cf;
  color: #696969;

  font-size: 20px;
  font-weight: 100;
}

h1,
h2,
h3,
h4 {
  padding: 0 20px;
  margin: 1.5em 0 1em 0;
  text-align: left;
  color: #004b8c;
}

/******************************************************************/
form {
  width: 100%;
  font-size: 16px;
  text-align: left;
  padding: 20px 20px;
}
form,
form * {
  box-sizing: border-box;
}
label {
  font-weight: bold;
  color: #004b8c;
  cursor: pointer;
}
.main-label {
  display: block;
  margin: 25px 0 0 0;
}
input[type="text"],
input[type="number"],
input[type="date"],
textarea,
select {
  width: 100%;
  display: block;
  padding: 8px;
  margin: 5px 0 0 0;
  font-size: 16px;
  transition: border-color 1s ease;
}
input[type="number"],
input[type="date"],
select {
  padding-left: 30px;
}
option {
  position: relative;
}
input[type="radio"] {
  margin: 0 10px 0 0;
  display: inline-block;
  vertical-align: middle;
}
input[type="radio"] + label {
  display: inline-block;
  vertical-align: middle;
}
textarea {
  padding-bottom: 20px;
}
textarea + span {
  position: absolute;
  bottom: 8px;
  right: 8px;
  color: rgb(0, 120, 0);
}
#radTransferType_ott {
  margin-top: 5px;
}
fieldset {
  border: none;
  padding: 0;
  margin: 0;
  position: relative;
}
fieldset .fa {
  position: absolute;
  bottom: 11px;
  left: 8px;
  text-align: center;
}
input[type="date"] ~ .fa {
  bottom: 13px;
}
.half-width {
  display: inline-block;
  width: 45%;
  vertical-align: top;
  margin: 0 5% 10px 0;
}
.half-width.right {
  margin: 0 0 10px 5%;
}
.button-holder {
  margin-top: 30px;
  text-align: right;
}
.error {
  color: red !important;
}
.error label:after {
  content: "(Required)";
  color: red;
  display: inline-block;
  margin-left: 5px;
}
.error input[type="text"],
.error input[type="date"],
.error input[type="number"],
.error select {
  border: 1px solid red;
}

/*****************************Modal********************************/
.modalWindow {
  position: fixed;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  height: 100%;
  text-align: center;
  z-index: 2;
}
.modalWindow .modal-content {
  display: block;
  text-align: left;
  background: white;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.5);
  max-width: 90%;
  max-height: 90%;
  width: 600px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 16px;
  z-index: 2;
}
.modalWindow * {
  box-sizing: border-box;
}
.modalWindow .modal-body {
  max-height: 350px;
  position: relative;
  overflow: auto;
}
.modalWindow h3 {
  padding: 2em;
  margin: 0;
  text-align: center;
  background: #004b8c;
  color: white;
}
.modalWindow .button-holder {
  padding: 0 2em 1em 2em;
}
button.close-button {
  background: #000;
  color: #fff;
  border: 1px solid white;
  border-radius: 200em;
  padding: 10px;
  display: block;
  position: absolute;
  left: 100%;
  top: 0;
  transform: translate(-50%, -50%);
}
button.close-button:after {
  font-size: 16px;
  content: "x";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
dl {
  margin: 0;
  padding: 0;
  padding: 0 2em 1em 2em;
}
dl div {
  font-size: 0;
  word-break: break-all;
  hyphens: auto;
}
dt {
  display: inline-block;
  width: 30%;
  font-size: 16px;
}
dd {
  display: inline-block;
  width: 70%;
  font-size: 16px;
}
dd,
dt {
  padding: 5px;
  margin: 0;
  vertical-align: top;
}
/****************************Confirm******************************/
.confirm {
  text-align: left;
}
.confirm * {
  box-sizing: border-box;
}
.confirm dl {
  background: white;
  border-radius: 0 0 10px 10px;
}
.confirm h3 {
  margin: 1em 0 0 0;
  padding: 0;
  background: #fff;
  padding: 20px 1.8em;
  border-radius: 10px 10px 0 0;
}

.confirm .button {
  width: auto;
}
.confirm .button-holder {
  margin: 30px 20px 20px 20px;
}
/****************************Notice*******************************/
.notice {
  border: 1px solid gold;
  background: lightyellow;
  padding: 10px;
  color: goldenrod;
  text-align: left;
  position: relative;
  border-radius: 10px;
  margin: 20px;
}

.notice.success {
  border-color: green;
  color: green;
  background: lightgreen;
}
.notice .fa {
  display: block;
  font-size: 2em;
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translate(0, -50%);
}
.notice p {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  padding: 0 10px 0 40px;
}
/************************Transfer-Activity**************************/
.transfer-activity h3,
.transfer-activity h4 {
  padding: 0;
}
.transfer-activity h3 {
  margin: 0 0 1.5em 0;
}
.transfer-activity h4 {
  text-align: left;
  margin-top: 1em;
}
.transfer-activity tbody tr {
  cursor: pointer;
}
.table-hover > tbody > tr:hover {
  background-color: #fff;
}
/****************************Profile*******************************/
.profile {
  padding: 20px;
}
.profile h3,
.profile h4 {
  padding: 0;
  margin-top: 8px;
}
.profile-picture {
  display: block;
  width: 100px;
  height: 100px;
  border: 1px solid #b1b1b1;
  color: #b1b1b1;
  padding: 0;
  margin: 10px auto;
  font-size: 100px;
  position: relative;
}
.profile-picture .fa {
  position: absolute;
  display: block;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.profile-content {
  margin: 0;
  padding: 0;
  padding-top: 20px;
}
.profile-content li {
  list-style: none;
  text-align: left;
}
.profile img {
  width: 100%;
  height: auto;
  margin-bottom: 10px;
}

/*Table*/
.transfer-activity-table {
  width: 100%;
  overflow-x: auto;
  margin-bottom: 5%;
}
.transfer-activity-table table {
  width: 100%;
  max-width: 100%;
  min-width: 600px;
  text-align: center;
  border-collapse: collapse;
  margin-top: 10px;
}
.transfer-activity-table thead {
  background: #004b8c;
  color: white;
}
.transfer-activity-table tbody tr:hover {
  background: #ddd;
}
.transfer-activity-table th {
  padding: 1em 10px;
}
.transfer-activity-table td {
  padding: 1em 10px;
}

/******************************************************************/
.mainSection {
  width: calc(100% - 100px);
  display: inline-block;

  margin: 25px;
  background-color: #fff;
  margin-top: 45px;
}

/* Main Footer and Content */

.mainFooter {
  width: 100%;
  background-color: #f6f9fa;

  min-height: 300px;
}

.firstLevelFooter {
  background-color: #f2f2f2;
  height: 84px;
}

.secondLevelFooter {
  min-height: 36px;

  background: #004b8c;

  color: WHITE;
  text-align: center;

  font-weight: bold;
  font-size: 12px;
}

.secondLevelFooter a {
  display: inline-block;
  position: relative;
  height: 100%;
  padding: 11px;
  color: WHITE;
  text-decoration: none;
}

.secondLevelFooter a:hover {
  background-color: #d0e5f4;
  color: #00233b;
  cursor: pointer;
}

.thirdLevelFooter {
  height: 275px;
  background-color: #f6f9fa;
  position: relative;
}

/* Flying Popup (fixed at bottom) */

.flyPopup {
  color: WHITE;
  background-color: #003a57;

  position: fixed;
  bottom: 0;

  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;

  border-top-left-radius: 10px;
  border-top-right-radius: 10px;

  width: inherit;

  font-size: 10px;
  padding: 10px;
}

.footerCloseBtn {
  position: absolute;
  right: -5px;
  top: -5px;

  color: GREEN;
  background-color: WHITE;

  border: solid 1px gray;

  width: 10px;
  height: 10px;

  text-align: center;
}

#chkShowFooter {
  position: fixed;
  top: 0;
}

#chkShowFooter + .flyPopup {
  display: none;
}

#chkShowFooter:checked + .flyPopup {
  display: block;
}

/* I want to... menu */

.lblOpenMenu {
  color: WHITE;
}

.wantToContainer {
  position: static;
  display: none;
  float: right;
  right: 0;
  background-color: #f6f6f6;
  border: 1px solid #e9e9e9;
  width: 209px;

  padding-bottom: 15px;
  margin-top: 15px;
}

.wantToContainer ul {
  margin: 0 25px;
  width: 158px;
  padding: 0;
  border-bottom: 1px dotted #b8b8b8;
}

.wantToContainer li {
  font-size: 12px;
  color: #0b6997;

  /*background; url(https://statmcstg.usaa.com/mcontent/static_assets/Media/control_sprite.png) no-repeat -13000px -2px;*/
  list-style: none;

  padding: 0 0 10px 14px;
}

.wantToContainer a {
  color: inherit;
  text-decoration: none;
}

.wantToContainer a:hover {
  text-decoration: underline;
}

.wantToContainer h4 {
  font-weight: bold;
  text-transform: uppercase;
  margin: 5px;

  color: #666666;
  font-size: 11px;
}

/* UTILITIES */

.hide {
  display: none;
}

.smallDisplay {
  display: none;
}

.bigDisplay {
  display: block;
}
.button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0 5px;
  padding: 10px;
  width: 100px;
  border: 0;
  color: white;
  cursor: pointer;
  font-size: 16px;
}
.simpleButton {
  background: #b00d20;
}

.simpleButton:hover {
  background: #da132a;
}

.CTAButton {
  background: #004b8c;
}

.CTAButton:hover {
  background: #0f74cc;
}

@media screen and (max-width: 600px) {
  .half-width,
  .half-width.right {
    width: 100%;
    margin: 20px 0 0 0;
  }
}

@media screen and (max-width: 480px) {
  dl dt,
  dl dd {
    width: 100%;
    margin-top: 5px;
  }
  dl dd {
    text-align: right;
  }

  .mainSection {
    width: 90%;
    margin: 25px;
  }

  .wantToContainer {
    padding: 0;
    margin: 0;
    top: 0;

    width: 100%;
    position: fixed;

    z-index: 5;

    overflow: hidden;

    transition: height 400ms linear;
    -webkit-transition: height 400ms linear;
    -moz-transition: height 400ms linear;
    -o-transition: height 400ms linear;
    transition: height 400ms linear;
  }

  .wantToContainer ul {
    border: none;
  }

  .wantToContainer li {
    display: inline-block;
    width: 100%;
  }

  .lblOpenMenu {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 10;
  }

  /* Menu control for phone-size display */

  #chkOpenMenu + label + aside {
    overflow: hidden;
    height: 0;
  }

  #chkOpenMenu:checked + label + aside {
    height: 140px;
  }

  #chkOpenMenu + label .openItem {
    display: block;
  }

  #chkOpenMenu:checked + label .openItem {
    display: none;
  }

  #chkOpenMenu + label .closeItem {
    display: none;
    color: BLACK;
  }

  #chkOpenMenu:checked + label .closeItem {
    display: block;
  }

  /* small utilities */

  .smallDisplay {
    display: block;
  }

  .bigDisplay {
    display: none !important;
  }
}
